<template>
  <div>

    <div class="wrapper">
      <div class="LeftSideBar mobile-spe">
        <LeftSideInfo />
      </div>
      <div class="CenterSideBar lighterBorder br">
        <tweet :userData="userData" :signedIn="signedIn"></tweet>
        <router-view />
      </div>
      <div class="RightSideBar">
        <RightSideInfo />
      </div>
      <button v-if="signedIn" data-toggle="modal"
              data-target="#edit-my-poll-popup"
              @click="setMatchId"
              class="bg-mdd-2 tweet-btn-2">
        <i class="las la-feather-alt"></i>
      </button>
      <MobileNavButtons v-if="signedIn"/>
    </div>

    <Whereto :whereto="whereto" @goBack="() => { whereto = false; }" />
    <review-box></review-box>
    <auth-modal></auth-modal>
  </div>
</template>

<script>
import LeftSideInfo from "../../components/Home/LeftSideInfo";
import RightSideInfo from "../../components/Home/RightSideInfo";
// eslint-disable-next-line no-unused-vars
import Footer from "../../components/Home/Footer";
import Whereto from "../../components/Modals/whereto";
// eslint-disable-next-line no-unused-vars
import ls from "../../localstorage";
import reviewBox from "../../components/Modals/reviewSystems/reviewBox";
import AuthModal from "../../components/AuthModal";
import tweet from "../../components/Home/tweet";
import MobileNavButtons from "../../components/Home/MobileNavButtons";

var pageData = {
  projectName: "",
  whereto: false,
    signedIn: false,
    userData: '',
};

export default {
  name: "Home",
  components: {
    MobileNavButtons,
    LeftSideInfo,
    RightSideInfo,
    Whereto,
    reviewBox,
      AuthModal,
      tweet,
  },
  data() {
    return pageData;
  },
  methods: {
      checkUserData(userData) {
          if (userData !== "" || userData !== "undefined") {
              this.userData = JSON.parse(userData);
              this.signedIn = true;
          }
      },
    showLoading() {
      let loader = this.$loader(this, this.$refs.loadContents);
      return loader;
    },
    // set match id for the review box
    setMatchId() {
      let fixture_id = this.$route.params.fixture_id;
      let smatchId = this.$route.params.match_id;
      let matchId;

      if (fixture_id !== undefined) {
        matchId = fixture_id
      }else if(smatchId !== undefined) {
        matchId = smatchId
      }else {
        matchId = 0
      }

      const data = {
        type: 'review',
        matchId: matchId.toString(),
      };
      window.eventBus.$emit("review_option", data);
    },
  },
  mounted() {
	// loader = this.showLoading();
    let $ = this.$Jquery;
    this.projectName = this.$store.state.projectName;

    window.eventBus.$on("nowhereto", id => {
      this.whereto = id;
    });

      var userData = localStorage.fansData;
      if (userData) {
          this.checkUserData(userData);
      }

    $("#site-header").removeClass("switched");

    // $('.CenterSideBar').animate({
    //   scrollTop: $('.CenterSideBar').scrollHeight - $('.CenterSideBar').clientHeight,
    // }, 500);

    // this.getUser();
    // $(".back-to-top").on("click", function() {
    //   $("html,body").animate(
    //     {
    //       scrollTop: 0
    //     },
    //     1200
    //   );
    //   return false;
    // });

  },
};
</script>
<style>
  .LeftSideBar {
    width: 25%;
    position: -webkit-sticky !important;
    position: sticky !important;
    top: 0;
    padding: 0 20px;
    height: 100vh;
    z-index: 1;
  }
  .wrapper {
    width: 67%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
  .CenterSideBar {
    width: 50%;
  }
  .RightSideBar {
    width: 25%;
    position: -webkit-sticky !important;
    position: sticky !important;
    top: 0;
    height: 100vh;
  }
  .h-100 {
    min-height: 200vh !important;
  }

</style>