<template>
  <!-- Faqs Popup -->

<div class="modal fade" id="faqs-popup" tabindex="-1" role="dialog" aria-labelledby="faqs-popup" aria-hidden="true">
	<div class="modal-dialog window-popup faqs-popup" role="document">
		<div class="modal-content">
			<div class="modal-body">
				<a href="#" class="close icon-close" data-dismiss="modal" aria-label="Close">
					<svg class="olymp-close-icon">
						<use xlink:href="svg-icons/sprites/icons.svg#olymp-close-icon"></use>
					</svg>
				</a>
				<div class="whereto">
					<div class="wheretoOptionsBox">
						<div class="box" @click="setChooseLeague">
							<i class="las la-trophy"></i>
							<p>Choose League</p>
						</div>

						<div class="box" @click="setSearchLeague">
							<i class="las la-search"></i>
							<p>Search Team</p>
						</div>

						<div class="box" @click="setSearchLeagueDate">
							<i class="lar la-calendar-alt"></i>
							<p>Choose Date</p>
						</div>
					</div>

					<whereto-choose-league :active="chooseLeague"></whereto-choose-league>
					<whereto-search-league :active="searchLeague"></whereto-search-league>
					<whereto-search-league-date :active="searchLeagueDate"></whereto-search-league-date>

				</div>
			</div>
		</div>
	</div>
</div>

<!-- ... end Faqs Popup -->
</template>

<script>
	import WheretoChooseLeague from "./wheretoChooseLeague";
	import WheretoSearchLeague from "./wheretoSearchLeague";
	import WheretoSearchLeagueDate from "../wheretoSearchLeagueDate";
var loader;

var pageData = {
	errorData: {},
	chooseLeague: false,
	searchLeague: false,
	searchLeagueDate: false,
};

export default {
	name: "Whereto",
	data() {
		return pageData;
	},
	components: {
		WheretoSearchLeagueDate,
		WheretoSearchLeague,
		WheretoChooseLeague,
	},
    methods: {
		setChooseLeague() {
			$('.wheretoOptionsBox').hide()
			this.chooseLeague = true
		},
		setSearchLeague() {
			$('.wheretoOptionsBox').hide()
			this.searchLeague = true
		},
		setSearchLeagueDate() {
			$('.wheretoOptionsBox').hide()
			this.searchLeagueDate = true
		},
    },
    props: {
        whereto: Boolean,
    },
    mounted() {
		// $('#faqs-popup').modal('show')
		$('#faqs-popup').on('hidden.bs.modal', function () {
			window.eventBus.$emit('wheretoBackFromComp', true)
		})
		window.eventBus.$on('wheretoBackFromComp', val => {
			if (val === true) {
				$('.wheretoOptionsBox').show()
				this.chooseLeague = false;
				this.searchLeague = false;
				this.searchLeagueDate = false;
			}
		})
    },
    watch: {

    },
}
</script>

<style>
	.wheretoOptionsBox {
		width: 100%;
		padding: 20px;
		display: flex;
		flex-direction: column;
	}
	.wheretoOptionsBox .box {
		width: 100%;
		padding: 20px;
		background-color: white;
		border-radius: 6px;
		margin-bottom: 20px;
		text-align: center;
		box-shadow: 2px 2px 10px rgba(0,0,0,.1);
		cursor: pointer;
		transition: all 0.3s ease-in-out;
	}
	.wheretoOptionsBox .box:hover {
		box-shadow: 2px 2px 20px rgba(0,0,0,.2);
	}
	.wheretoOptionsBox .box i {
		font-size: 30px;
	}
	.wheretoOptionsBox .box p {
		font-size: 20px;
	}
.form-group.label-floating label.control-label, .form-group.label-placeholder label.control-label {
    top: 18px;
    /* font-size: 21px !important; */
    line-height: 1.42857;
    left: 20px;
}

.form-group.label-static label.control-label, .form-group.label-floating.is-focused label.control-label, .form-group.label-floating:not(.is-empty) label.control-label, .form-group.has-bootstrap-select label.control-label {
    top: 10px;
    font-size: 11px !important;
    line-height: 1.07143;
}
.whereto {
	padding: 10px 30px;
}
.fix {
	float: left;
	width:100%;
	text-align: center;
	padding: 20px 0;
}
.fix img {
	margin: 0 auto !important;
}
.fix span {
	width: 100%;
	text-align: center;
	float: left;
}
.vs {
	width: 100%;
	padding: 30px 10px;
	height: 100%;
	display: flex;
	justify-content: center;
}
.viewMore {

}
.whatmatch a {
	color: inherit;
	width: 100%;
	transition: transform 1.9s ease-in-out;
}
.whatmatch a:hover {
	color:#08ddc1;
}
.whatmatch a:hover .fix img {
	transform: scale(1.3);
}
	.options_box {
		display: flex;
		justify-content: space-between;
		padding: 10px 30px;
	}
</style>