<template>
    <div v-show="active" style="min-height: 400px">
        <i @click="clickBackButton" class="las la-long-arrow-alt-left" style="float: left;font-size: 37px;margin-right: 10px;position: relative;z-index: 2;cursor: pointer"></i>
        <date-picker v-model="time1" valueType="format" placeholder="Search League by Date"></date-picker>
        <div class="hide" id="searchByDateLoader" style="width:100%;text-align:center">
            <img src="/image/lg.gooey-ball-lodaer.gif" width="60" />
        </div>
        <br><br><br>
        <response-message :data="errorData" />
        <div v-for="(fixture, index) in fixtures" :key="index" class="post__author author vcard inline-items whatmatch">
            <router-link :to="{ name: 'Match', params: {fixture_id: fixture.match_key} }">
                <div @click="getSelectedMatch(fixture)" class="author-date" style="width:100%">
                    <div class="post__date">
                        <time class="published" datetime="2017-03-24T18:18">
                            {{  $getDateYear(fixture.match_date) }}
                        </time>
                    </div>
                    <div style="width: 100%;display: flex;justify-content: space-between">
                        <div class="fix">
                            <img :src="fixture.home_team_badge" alt="author">
                            <span>{{fixture.home_team_name}}</span>
                        </div>
                        <div class="vs">VS</div>
                        <div class="fix">
                            <img :src="fixture.away_team_badge" alt="author">
                            <span>{{fixture.away_team_name}}</span>
                        </div>
                    </div>

                </div>
            </router-link>
        </div>

    </div>
</template>

<script>
    import DatePicker from 'vue2-datepicker';
    import 'vue2-datepicker/index.css';
    export default {
        name: "wheretoSearchLeagueDate",
        data() {
            return {
                time1: '',
                errorData: {},
                loading: false,
                selectedLeague: '',
                firstPageUrl: '',
                lastPageUrl: '',
                nextPageUrl: '',
                total: '',
                fixtures: [],
                hasFixtures: false,
            }
        },
        components: {
            DatePicker,
        },
        watch: {
            time1(val) {
                this.searchByDate(val)
            },
            active(val) {
                if (val) {
                    this.showWhereto()
                }
            },
        },
        props: {
            active: Boolean,
        },
        mounted() {
            window.eventBus.$on('wheretoBackFromComp', val => {
                if (val === true) {
                    this.fixtures = []
                }
            })
        },
        methods: {
            clickBackButton() {
                window.eventBus.$emit('wheretoBackFromComp', true)
            },
            getSelectedMatch(feed) {
                $('#faqs-popup').modal('hide');
                this.$store.commit('selectedFeed', feed)
            },
            showWhereto(nurl = null) {
                var fetchUrl;
                if(nurl !== null) {
                    fetchUrl = nurl;
                }else {
                    fetchUrl = "matches/all/10";
                }
                $("#searchByDateLoader").removeClass("hide");
                this.axios.get(fetchUrl)
                    .then(response => {
                        $("#searchByDateLoader").addClass("hide");
                        // console.log(response.data.data);
                        let content;
                        let fixtures = response.data.data.matches.data;
                        // check if the result is more than the returned data
                        this.moreResult = response.data.data.more;
                        // bind fixtures to the data
                        if (fixtures.length > 0) {
                            this.hasFixtures = true;
                            this.fixtures = fixtures;
                        }else {
                            this.hasFixtures = false;
                        }
                        // pagination path
                        this.firstPageUrl = response.data.data.matches.first_page_url;
                        this.lastPageUrl = response.data.data.matches.last_page_url;
                        this.nextPageUrl = response.data.data.matches.next_page_url;
                        this.total = response.data.data.matches.total;
                    })
                    .catch(error => {
                        $("#searchByDateLoader").addClass("hide");
                        this.errorData = {
                            message: error.response
                                ? error.response.data.message + " Sorry we had a problem getting the data requested for"
                                : "Sorry an error occured, check your internet",
                            isError: true
                        };
                    })
            },
            searchByDate(date) {
                $("#searchByDateLoader").removeClass("hide");
                this.axios.get(`/matches/by_date/${date}/20`)
                .then((res) => {
                    this.fixtures = []
                    $("#searchByDateLoader").addClass("hide");
                    const data = res.data.data
                    this.total = data.page_total
                    this.nextPageUrl = data.matches.next_page_url
                    this.fixtures = data.matches.data
                    console.log(data.matches.data)
                })
                .catch((err) => {
                    $("#searchByDateLoader").addClass("hide");
                    console.log(err)
                })
            },
        },
    }
</script>

