<template>
    <div class="wheretoChooseLeagueComp" v-show="active" style="min-height: 400px">
        <i @click="clickBackButton" class="las la-long-arrow-alt-left" style="float: left;font-size: 37px;margin-right: 10px;position: relative;z-index: 2;cursor: pointer"></i>
        <h1 style="font-size: 15px;margin-bottom: 10px;float: left"> {{ selectedLeague === '' ? 'Select League' : selectedLeague }}</h1>
        <div class="hide" id="chooseLeagueLoader" style="width:100%;text-align:center">
            <img src="/image/lg.gooey-ball-lodaer.gif" width="60" />
        </div>
        <br><br><br>

        <div v-show="allLeagues.data && allLeagues.data.length > 0" >
            <div :class=" 'chooseLeagueBox ' + randomClass() " v-for="(leagues,index) in allLeagues.data" :key="index" @click="selectLeague(leagues.league_key,leagues.league_name)">
                <p>{{ leagues.league_name }}</p>
            </div>
        </div>
        <div v-show="fixtures.length > 0">
            <div v-for="(fixture, index) in fixtures" :key="index" class="post__author author vcard inline-items whatmatch">
                <router-link :to="{ name: 'Match', params: {fixture_id: fixture.match_key} }">
                    <div @click="getSelectedMatch(fixture)" class="author-date" style="width:100%">
                        <div class="post__date">
                            <time class="published" datetime="2017-03-24T18:18">
                                {{  $getDateYear(fixture.match_date) }}
                            </time>
                        </div>
                        <div style="width: 100%;display: flex;justify-content: space-between">
                            <div class="fix">
                                <img :src="fixture.home_team_badge" alt="author">
                                <span>{{fixture.home_team_name}}</span>
                            </div>
                            <div class="vs">VS</div>
                            <div class="fix">
                                <img :src="fixture.away_team_badge" alt="author">
                                <span>{{fixture.away_team_name}}</span>
                            </div>
                        </div>

                    </div>
                </router-link>
            </div>
        </div>
        <div v-show="loading === false && nope === true" style="text-align: center">
            <p style="width: 100%">Opps! no record on {{selectedLeague}}</p>
            <button @click="getAllLeagues" style="border-radius: 30px;padding: 10px;text-align: center;border:solid 2px #0c5460;color:#0c5460">Reload leagues</button>
        </div>

<!--        <button id="moreR" v-if="choosenLeagues.total > 10 || allLeagues.total > 10" @click="showMore()" class="btn btn-block bg-mdd">More</button>-->

    </div>
</template>

<script>
    export default {
        name: "wheretoChooseLeague",
        data() {
            return {
                allLeagues: {
                    next_page_url: '',
                    last_page_url: '',
                    first_page_url: '',
                    data: [],
                    total: '',
                },
                choosenLeagues: {
                    firstPageUrl: '',
                    lastPageUrl: '',
                    nextPageUrl: '',
                    total: '',
                },
                fixtures: [],
                hasFixtures: false,
                selectedLeague: '',
                loading: false,
                nope: false,
            }
        },
        props: {
            active: Boolean,
        },
        watch: {
            active(val) {
                if (val) {
                    this.loading = true
                    this.getAllLeagues()
                }
            },
        },
        methods: {
            clickBackButton() {
                window.eventBus.$emit('wheretoBackFromComp', true)
            },
            showMore() {
                $('.moreR').html('Loading...');
                if (this.choosenLeagues.total > 10) {
                    console.log('load more choosen league')
                    this.loadMoreChoosenLeague()
                }else if (this.allLeagues.total > 10) {
                    console.log('load more all league')
                    this.loadMoreLeague()
                }
            },
            getSelectedMatch(feed) {
                $('#faqs-popup').modal('hide');
                this.$store.commit('selectedFeed', feed)
            },
            randomClass() {
                // set classes
                const classes = new Array ('clb_blue', 'clb_black', 'clb_red', 'clb_green', 'clb_brown', 'clb_gold');
                // calculate length once, as this will never change
                const length = classes.length;
                // select all a-tags
                const rand = classes[ Math.floor ( Math.random() * length ) ]
                return rand

            },
            loadMoreLeague() {
                const url = this.allLeagues.next_page_url;
                this.axios.get(url)
                    .then(res => {
                        $('#moreR').html('Load More');
                        $(".wheretoChooseLeagueComp").animate({ scrollTop: 0 }, "fast");

                        const data = res.data.data.leagues;
                        this.allLeagues.first_page_url = data.first_page_url;
                        this.allLeagues.last_page_url = data.last_page_url;
                        this.allLeagues.next_page_url = data.next_page_url;
                        this.allLeagues.data = data.data;
                        this.allLeagues.total = data.total;
                        this.hasFixtures = true;
                        console.log(data.data)
                    })
                    .catch(() => {
                        $('.moreR').html('Load More');
                        this.errorData = {
                            message: error.response
                                ? error.response.data.message + " Sorry we had a problem getting the data requested for"
                                : "Sorry an error occured, check your internet",
                            isError: true
                        };
                    })
            },
            loadMoreChoosenLeague() {
                const url = this.choosenLeagues.nextPageUrl;
                this.axios.get(url)
                    .then(res => {
                        $('#moreR').html('Load More');
                        $(".wheretoChooseLeagueComp").animate({ scrollTop: 0 }, "fast");

                        const data = res.data.data.matches
                        this.choosenLeagues.nextPageUrl = data.next_page_url;
                        this.choosenLeagues.lastPageUrl = data.last_page_url;
                        this.choosenLeagues.firstPageUrl = data.first_page_url;
                        this.fixtures = data.data
                        console.log(data.data)
                    })
                    .catch(() => {
                        $('.moreR').html('Load More');
                        this.errorData = {
                            message: error.response
                                ? error.response.data.message + " Sorry we had a problem getting the data requested for"
                                : "Sorry an error occured, check your internet",
                            isError: true
                        };
                    })
            },
            selectLeague(league_key, league_name) {
                $("#chooseLeagueLoader").removeClass("hide");
                this.selectedLeague = league_name
                this.axios.get(`/leagues/matches/${league_key}/10`)
                    .then(res => {
                        $("#chooseLeagueLoader").addClass("hide");
                        const data = res.data.data.matches
                        this.choosenLeagues.nextPageUrl = data.next_page_url;
                        this.choosenLeagues.lastPageUrl = data.last_page_url;
                        this.choosenLeagues.firstPageUrl = data.first_page_url;
                        this.choosenLeagues.total = res.data.data.page_total;
                        if (res.data.data.page_total > 0) {
                            this.loading = false;
                            this.nope = false;
                            this.hasFixtures = true;
                            this.allLeagues = {};
                            this.fixtures = data.data
                            // console.log(data.data)
                        }else {
                            this.hasFixtures = false;
                            this.allLeagues = {};
                            this.fixtures = [];
                            this.loading = false;
                            this.nope = true;
                        }
                    })
                    .catch((error) => {
                        $("#chooseLeagueLoader").addClass("hide");
                        this.selectedLeague = '';
                        this.errorData = {
                            message: error.response
                                ? error.response.data.message + " Sorry we had a problem getting the data requested for"
                                : "Sorry an error occured, check your internet",
                            isError: true
                        };
                    })
            },
            getAllLeagues() {
                $("#chooseLeagueLoader").removeClass("hide");
                this.axios.get('leagues/all/10')
                    .then(res => {
                        $("#chooseLeagueLoader").addClass("hide");
                        this.loading = false;
                        this.nope = false;
                        const data = res.data.data.leagues;
                        this.allLeagues.first_page_url = data.first_page_url;
                        this.allLeagues.last_page_url = data.last_page_url;
                        this.allLeagues.next_page_url = data.next_page_url;
                        this.allLeagues.data = data.data;
                        this.allLeagues.total = data.total;
                        this.choosenLeagues = {};
                        // console.log(data.data)
                    })
            },
        },
        mounted() {
            window.eventBus.$on('wheretoBackFromComp', val => {
                if (val === true) {
                    this.allLeagues = []
                    this.choosenLeagues = {}
                    this.fixtures = []
                }
            })
        },
    }
</script>

<style scoped>
.chooseLeagueBox {
    width: 100%;
    padding: 15px 20px;
    border-radius: 30px;
    box-shadow: 0 5px 8px rgba(0,0,0,.2);
    margin-bottom: 20px;
    cursor: pointer;
}
    .chooseLeagueBox p {
        margin: 0;
        font-size: 13px;
    }

    .clb_blue {
        background-color: #0BA4E0;
        color: #fff;
    }
    .clb_black {
        background-color: #0b0b0b;
        color: #fff;
    }
.clb_red {
    background-color: #8c1a32;
    color: #fff;
}
.clb_green {
    background-color: #0c5460;
    color: #fff;
}
.clb_brown {
    background-color: #a32b2b;
    color: #fff;
}
.clb_gold {
    background-color: #bb890e;
    color: #fff;
}

</style>