<template>
    <div class="ui-block lighterBorder" v-if="hasLiveMatch">
        <div class="ui-block-title">
            <h6 class="title" style="color: #fff">Live Matches to Follow</h6>
            <router-link to="/livematch">View All</router-link>
        </div>
        <div class="ui-block-content showLiveMatches">
            <div @click="goToMatch(feed)" v-for="(feed, index) in liveFeed" :key="index" class="liveMatchesBox lighterBorder" style="background-image:url('/image/epl_refree.jpg'); background-blend-mode: overlay;background-color: rgba(0,0,0,0.63);">
                <div class="liveMatchesBox_header">
                    <p v-html="feed.match_date"></p>
                    <p v-html="feed.league_name"></p>
                </div>
                <div class="liveMatchesBox_body">
                    <img :src="feed.home_team_badge">
                    <div class="caption">
                        <p>( {{ feed.hometeam_score }} - {{feed.awayteam_score}} )</p>
                    </div>
                    <img :src="feed.away_team_badge" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "LiveMatchWidget",
        data() {
            return {
                hasLiveMatch: false,
                liveFeed: {},
            }
        },
        methods: {
            goToMatch(feed) {
                const matchId = this.$route.params.match_id
                if (matchId !== undefined) {
                    window.eventBus.$emit('loadLiveMatch', feed.match_key)
                }else {
                    this.$router.push(`/livematch/${feed.match_key}`)
                }
            },
            getLivematches() {
                this.axios.get('matches/live/2')
                    .then(res => {
                        const liveStatus = res.data.data.status;
                        if(liveStatus === true) {
                            this.hasLiveMatch = liveStatus;
                            const matches = res.data.data.matches;
                            this.liveFeed = matches.data
                        }
                    })
                    .catch(err => {
                        // console.log(err)
                        return err
                    })
            },
        },
        mounted() {
            this.getLivematches();
            setInterval( this.getLivematches(), 30000 )
        },
    }
</script>

<style scoped>
    .showLiveMatches {
        position: relative;
        padding: 10px;
        overflow: auto;
        max-height: 300px;
    }
    .liveMatchesBox {
        width: 100%;
        height: 100px;
        border-radius: 6px;
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
        margin-bottom: 20px;
        cursor: pointer;
    }
    .liveMatchesBox_header {
        width: 100%;
        display: flex;
        justify-content: space-between;
        padding:6px 10px;
    }
    .liveMatchesBox_header p {
        font-weight: bold;
        color: aliceblue;
        font-size: 90%;
        margin:0;
    }
    .liveMatchesBox_body {
        padding:10px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        height: 60%;
        border-color: #fff;
    }
    .liveMatchesBox_body img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
    .liveMatchesBox_body .caption {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .liveMatchesBox_body .caption p {
        color: #fff;
        width: 100%;
        text-align: center;
        font-size: 15px;
        margin:0;
    }
</style>