<template>
    <div class="tweetBox">
        <div class="tweetTop" v-if="signedIn">
            <span v-if=" $route.name === 'NewsFeed' || to.name === 'NewsFeed' ">
                <img v-if="signedIn" @click="openSideBar" :src="getAvatarLocal()">
                <a href="javascript:;" v-else data-toggle="modal"
                   data-target="#registration-login-form-popup">
                    <img style="border-radius: 0" src="/image/new_logos/test_white.png">
                </a>
            </span>
            <span v-else>
                <div @click="goBack" class="icon-back">
                    <svg><use xlink:href="/svg-icons/sprites/icons.svg#olymp-stats-arrow"></use></svg>
                </div>
            </span>
            <h5 @click="reloadTweet">{{ $route.meta.title }}</h5>
            <img v-if="signedIn" src="/image/new_logos/test_white.png" class="brand" style="float: right">
            <i @click=" () => {showSearchBar = true} " class="userSearchIcon las la-search"></i>
            <div v-if="showSearchBar" class="searchFullBox">
                <user-search></user-search>
                <i style="top: -37px !important;font-size: 25px;right: 8px !important;z-index: 3" @click=" () => {showSearchBar = false} " class="userSearchIcon lar la-times-circle"></i>
            </div>
        </div>
        <div v-else class="no-user-box">
            <img src="/image/new_logos/test_white.png" class="brand">
            <input type="search" id="match-search" placeholder="Search Match" >
            <div>
                <button id="login" class="no-user-button no-user-outline" data-toggle="modal"
                        data-target="#registration-login-form-popup">Log in</button>
                <button class="no-user-button" data-toggle="modal"
                        data-target="#registration-login-form-popup">Sign up</button>
            </div>
        </div>
        <AuthModal></AuthModal>
    </div>
</template>

<script>
    import {mapState} from "vuex";
    import avta from "../../functions/getAvatar";
    import AuthModal from "../AuthModal";
    import ls from '../../localstorage';
    import UserSearch from "../UserSearch";

    export default {
        name: "tweet",
        data() {
          return {
              from: [],
              to: [],
              route: '',
              showSearchBar: false,
          }
        },
        components: {UserSearch, AuthModal },
        computed: {
            ...mapState(["fansData", "user", "isFromReferral"]),
            getUserDetails() {
                return this.user;
            },
            getUsername() {
                return this.user.username;
            },
            getAvatar() {
                return this.user.avatar;
            },
            getFromReferral() {
                return this.isFromReferral;
            },
        },
        watch: {
            $route(to, from) {
                this.from = from
                this.to = to
                this.route = this.$route.name
                // console.log('c route',this.$route.name)
            },
        },
        mounted() {
            // console.log('from tweet top', this.getRoute)
            this.setReferral()
        },
        updated() {
            // on updated request for route details
            // console.log(this.to.path + ' from ' + this.from.path)
        },
        props: ['userData','signedIn'],
        methods: {
            async setReferral() {
                // handle login button click if referral is set
                const isFromRef = this.getFromReferral
                const userDetails = await ls.getStorage('fansData')
                if(isFromRef) {
                    if (!userDetails[0]) {
                        $('#login').click()
                    }else {
                        this.$store.state.isFromReferral = false;
                        await this.$router.push('/')
                    }
                }
            },
            goBack() {
                const path = this.from.path;
                if (path === undefined) {
                    this.$router.push('/')
                }else {
                    this.$router.push(path)
                }
            },
            openSideBar() {
                $('.mobile-spe').show()
                $('.mobile-special-link').show()
                $('.mobile-rack').animate({
                    left: 0
                }, 500)
            },
            getAvatarLocal() {
                // var userData = localStorage.fansData;
                return avta({state:{ av: this.getAvatar, us: this.getUsername }});
            },
            reloadTweet() {
                const route = this.$route
                $(".CenterSideBar").animate({ scrollTop: 0 }, "fast");
                if(route.path === '/') {
                    window.eventBus.$emit("loadFeeds", true);
                }
            },
        },
    }
</script>

<style scoped>
    .no-user-outline {
        border:solid 1px #ff5e3a;
        background-color: transparent !important;
        color: #ff5e3a !important;
    }
    .no-user-box div {
        display: flex;
        width: 100%;
        justify-content: space-between;
        padding: 10px 0;
    }
    .no-user-button:hover {
        background-color: #ff5e3a !important;
        color: #fff !important;
        outline: none;
    }
    .no-user-button {
        border-radius: 30px;
        width: 100%;
        padding: 10px 0;
        color: #fff;
        font-weight: bold;
        background-color: #ff5e3a;
        margin: 0 10px;
        outline: none;
    }
    .no-user-box {
        width: 100%;
        height: 100%;
        float: left;
        padding: 10px;
        background-color: #1c4d36 !important;
        border-bottom: solid 1px rgba(230, 236, 245, 0.2) !important;
        margin-bottom: 20px;
    }
    .no-user-box .brand {
        width: 30px;
        height: 30px;
        float: left;
    }
    .no-user-box input {
        float: right;
        width: calc(100% - 40px);
        padding: 8px 20px;
        border-radius: 30px;
        border: none;
        background-color: #2b6b4d !important;
        color: #eee !important;
        outline: none;
    }
    .no-user-box input::placeholder {
        color: #eee;
    }
.tweetTop span {
    float: left;
}
.tweetTop .brand {
    float: right;
    border-radius: 0;
}
.tweetTop .icon-back {
    width: 32px;
    height: 32px;
    float: left;
    background-color: rgba(255,255,255,0.2);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
}
    .tweetTop .icon-back svg {
        width: 16px;
        height: 20px;
        fill: #ffffff;
        transform: rotate(-90deg);
    }
    .userSearchIcon {
        float: right;
        font-weight: bold;
        font-size: 30px;
        color: #fff;
        position: relative;
        display: none;
    }
    .searchFullBox {
        position: absolute;
        width: 100%;
        left: 0;
        height: 100%;
        background-color: #1c4d36;
        top: 0;
        padding:0 10px;
    }
    @media(max-width: 768px) {
        .userSearchIcon {
            display: block;
        }
    }
</style>