<template>
  <!-- Left Sidebar -->
  <div class="">
    <div class="desk-special-link">
      <img src="/image/new_logos/test_white.png" class="logoBrand">

      <div class="main-side-avatar" v-if="signedIn === true">
        <img :src="getAvatarLocal()" alt="author" />
        <div class="main-side-avatar-details ">
          <router-link :to=" '/profile/' + $store.state.user.username" class="h5 author-name">@{{ $store.state.user.username }}</router-link>
          <div class="country" v-if="$store.state.user.club.length <= 0 ">No Club</div>
          <div class="country" v-else><img style="box-shadow:0 0 0 transparent;width: 15px;height: 15px;border-radius: 50%;margin-right: 5px;float: left;top: 0" :src="$store.state.user.club[0].team_badge">{{$store.state.user.club[0].team_name}}</div>
          <div @click="copyReferral" style="cursor: pointer" class="referral">
            {{ referralCode + getUsername }}
            <span style="position:absolute;right: 0;font-size: 12px;top: -13px;font-style: italic;" v-if="showCopyReferralResponse" v-html="copyReferralResponse"></span>
          </div>
          <div class="friend-count">
            <a href="#" class="friend-count-item">
              <div class="h6">{{ getUserContributions.reviews }}</div>
              <div class="title">Reviews</div>
            </a>
            <a href="#" class="friend-count-item">
              <div class="h6">{{ getUserContributions.followings }}</div>
              <div class="title">Following</div>
            </a>
            <a href="#" class="friend-count-item">
              <div class="h6">{{ getUserContributions.followers }}</div>
              <div class="title">Followers</div>
            </a>
          </div>
        </div>
      </div>

      <div class="special-links" :style=" signedIn ? 'top: 34px;' : 'top: 0;' ">
        <ul>
          <li><router-link to="/" @click="setRefresh" exact-active-class="active"><i class="las la-home"></i><span>Home</span></router-link></li>
          <li><router-link :to="{name: 'Livematch'}" exact-active-class="active"><i class="las la-baseball-ball"></i><span>Live Match</span></router-link></li>
          <li><a href="javascript:;" id="whatMatch" data-toggle="modal"
                 @click="() => { whereto = true }"
                 data-target="#faqs-popup"><i class="la la-search"></i><span>Search Match</span></a></li>
          <li v-if="signedIn">
            <router-link to="/notifications" exact-active-class="active">
              <i class="las la-bell"></i>
              <span>Notifications</span>
              <span v-if="getNotificationsCount && getNotificationsCount.total > 0" class="badge badge-danger notibadge">{{ getNotificationsCount ? getNotificationsCount.total > 100 ? getNotificationsCount.total + '+' : getNotificationsCount.total : '' }}</span>
            </router-link>
<!--            <a-badge :count="getNotificationsCount.total" color="red">-->
<!--              -->
<!--            </a-badge>-->
          </li>
          <li v-if="signedIn"><router-link :to="`/profile/${getUsername}`" exact-active-class="active"><img :src="getAvatarLocal()" alt="author" /><span>Profile</span></router-link></li>
          <li v-if="signedIn"><a href="javascript:;" @click="setLogout(true)"><i class="las la-sign-out-alt"></i><span>Logout</span></a></li>
          <li v-else><a href="javascript:;"
                        data-toggle="modal"
                        data-target="#registration-login-form-popup" ><i class="las la-sign-in-alt"></i><span>Login</span></a></li>
        </ul>
        <button v-if="signedIn"
                data-toggle="modal"
                data-target="#edit-my-poll-popup"
                @click="setMatchId"
                class="bg-mdd-2 tweet-btn">Post a review</button>
      </div>

    </div>

    <div class="mobile-special-link mobile-spe" v-if="signedIn === true">
      <div class="mobile-rack">
        <div class="topHeader">
          <h1>Account info</h1>
          <div @click="closeSideBar" class="closeBtn">
            <svg class="olymp-close-icon">
              <use xlink:href="svg-icons/sprites/icons.svg#olymp-close-icon" />
            </svg>
          </div>
        </div>

        <div class="bodySection">
          <div class="ui" v-if="signedIn === true">
            <div class="main-side-avatar">
              <img :src="getAvatarLocal()" alt="author" />
              <div class="main-side-avatar-details ">
                <router-link :to=" '/profile/' + $store.state.user.username" class="h5 author-name">@{{ $store.state.user.username }}</router-link>
                <div class="country" v-if="$store.state.user.club.length <= 0 ">No Club</div>
                <div class="country" v-else><img style="box-shadow:0 0 0 transparent;width: 15px;height: 15px;border-radius: 50%;margin-right: 5px;top: 0" :src="$store.state.user.club[0].team_badge">{{$store.state.user.club[0].team_name}}</div>
                <div @click="copyReferral" style="cursor: pointer" class="referral">
                  {{ referralCode + getUsername }}
                  <span style="position:absolute;right: 0;font-size: 12px;top: -13px;font-style: italic;" v-if="showCopyReferralResponse" v-html="copyReferralResponse"></span>
                </div>
                <div class="friend-count">
                  <a href="#" class="friend-count-item">
                    <div class="h6">{{ getUserContributions.reviews }}</div>
                    <div class="title">Reviews</div>
                  </a>
                  <a href="#" class="friend-count-item">
                    <div class="h6">{{ getUserContributions.followings }}</div>
                    <div class="title">Following</div>
                  </a>
                  <a href="#" class="friend-count-item">
                    <div class="h6">{{ getUserContributions.followers }}</div>
                    <div class="title">Followers</div>
                  </a>
                </div>
              </div>
            </div>
          </div>

          <div class="mobile-special-link-link">
            <ul>
              <li><router-link to="/" @click="setRefresh" exact-active-class="active"><i class="las la-home"></i><span>Home</span></router-link></li>
              <li @click="closeSideBar"><a href="javascript:;" data-toggle="modal"
                                     @click="() => { whereto = true }"
                                     data-target="#faqs-popup" ><i class="las la-search"></i><span>Search Match</span></a></li>
              <li @click="closeSideBar" v-if="signedIn"><router-link to="/notifications" exact-active-class="active"><i class="las la-bell"></i><span>Notifications</span></router-link></li>
              <li @click="closeSideBar" v-if="signedIn"><router-link :to="{name: 'Livematch'}" exact-active-class="active"><i class="las la-baseball-ball"></i><span>Live Match</span></router-link></li>
              <li @click="closeSideBar" v-if="signedIn"><router-link :to="`/profile/${getUsername}`" exact-active-class="active"><i class="las la-user"></i><span>Profile</span></router-link></li>
              <li v-if="signedIn"><a href="javascript:;" @click="setLogout(true)"><i class="las la-sign-out-alt"></i><span>Logout</span></a></li>
            </ul>
          </div>
        </div>
      </div>
    </div>

  </div>
  <!-- ... end Left Sidebar -->
</template>

<script>
import avta from "@/functions/getAvatar";
import {mapState} from "vuex";

var pageData = {
  projectName: "",
  userData: {},
  id: null,
  avatar: "",
  signedIn: false,
  showReviewBox: false,
  dds: 1,
  referralCode: window.location.origin + '/r/',
  showCopyReferralResponse: false,
  copyReferralResponse: 'Copied',
};

export default {
  name: "LeftSideBar",
  data() {
    return pageData;
  },
  components: {},
  computed: mapState({
    getAvatar: state => state.user.avatar,
    getUsername: state=> state.user.username,
    getUserContributions: state => state.contributions,
    getNotificationsCount: state => state.notifications,
  }),
  methods: {
    copyReferral() {
      const username = this.getUsername;
      const url = window.location.origin;
      const shareUrl = `${url}/r/${username}`;
      const _this = this;
      const $body = document.getElementsByTagName('body')[0];
      const $tempInput = document.createElement('INPUT');
      $body.appendChild($tempInput);
      $tempInput.setAttribute('value', shareUrl)
      $tempInput.select();
      const $copy = document.execCommand('copy')
      if ($copy) {
        this.showCopyReferralResponse = true;
        setTimeout(function() {
          _this.showCopyReferralResponse = false;
        }, 2000)
      }else {
        this.copyReferralResponse = 'Error'
      }
      $body.removeChild($tempInput);
    },
    closeSideBar() {
      $('.mobile-rack').animate({
        left: '-100%',
      }, 500)
      $('.mobile-spe').hide()
      $('.mobile-special-link').hide()
    },
    setRefresh() {
      window.eventBus.$emit('loadFeeds', true)
    },
    setLogout(val) {
      if(val === true) {
        window.eventBus.$emit("logout", val);
      }
    },
    // set match id for the review box
    setMatchId() {
      let fixture_id = this.$route.params.fixture_id;
      let smatchId = this.$route.params.match_id;
      let matchId;

      if (fixture_id !== undefined) {
        matchId = fixture_id
      }else if(smatchId !== undefined) {
        matchId = smatchId
      }else {
        matchId = 0
      }

      const data = {
        type: 'review',
        matchId: matchId.toString(),
      };
      window.eventBus.$emit("review_option", data);

    },
    checkUserData(userData) {
      if (userData !== "" || userData !== "undefined") {
        this.userData = JSON.parse(userData);
        this.signedIn = true;
      }
    },
    currentPage() {
      let path = this.$route.path;
      if (path === "/") {
        this.showReviewBox = false;
      }else {
        this.showReviewBox = true;
      }
    },
    getAvatarLocal() {
      // let userData = localStorage.fansData;
      let data = avta({state:{ av: this.getAvatar, us: this.getUsername }});
      return data;
    },
  },
  // created() {
  //   window.eventBus.$on("changedPath", path => {
  //     if (path === "/") {
  //       this.showReviewBox = false;
  //     } else {
  //       this.showReviewBox = true;
  //     }
  //   });
  // },
  mounted() {
    this.currentPage();
    let userData = localStorage.fansData;
    if (userData) {
      this.checkUserData(userData);
    }

    this.projectName = this.$store.state.projectName;

  },
  watch: {
    showReviewBox() {
      let path = this.$route.name;
      if (path === "HomeDefaultPage") {
        this.showReviewBox = false;
      }
    },
  },
};
</script>

<style>
  .referral:hover {
    color: #0c5460;
  }
  .referral {
    position: relative;
  }
  .desk-special-link {
    display: block;
    float: left;
  }
  .mobile-special-link {
    display: none;
  }
.friend-avatar .author-thumb img {
  border: 4px solid #fff;
  display: block;
  width: 98px;
  height: 98px;
}
.friend-count {
  margin-bottom: 0 !important;
  display: flex;
}
.friend-item .swiper-container {
  padding-bottom: 0 !important;
}
.dd {
  color: #00AEEF;
  font-weight: bold;
}
.dd span {
  position: relative;
  top: -9px;
  left: 8px;
}
</style>
