<template>
  <!-- Right Sidebar -->

  <div
    class="col col-xl-12 col-12 rightSideBar">
    <div class="user-search-box">
      <user-search></user-search>
    </div>
    <LiveMatchWidget></LiveMatchWidget>
    <div class="ui-block" style="background-color: rgba(220, 224, 228, 0.3)">
      <div class="ui-block-content">
        <div class="widget w-about">
          <a href="/" class="logo">
            <div class="img-wrap">
              <img src="/image/new_logos/test_white.png" width="100" alt="Olympus" />
            </div>
            <div class="title-block">
              <h6 class="logo-title">{{projectName}}</h6>
              <div class="sub-title">SOCIAL NETWORK FOR FOOTBALL FANS</div>
            </div>
          </a>
          <p>Matchday.ng is the place to be for all your football pre-game, in-game and post game reviews. Follow the game, check scores, see goals, Get stats, Debate decisions, Best tips/odds, Chat with friends or strangers, Analyst reviews, all in real time.</p>
          <ul class="socials">
            <li>
              <a href="https://instagram.com/matchday.ng" target="_blank">
                <i class="fab fa-instagram" aria-hidden="true"></i>
              </a>
            </li>
            <li>
              <a href="https://www.facebook.com/matchdaydeals" target="_blank">
                <i class="fab fa-facebook-square" aria-hidden="true"></i>
              </a>
            </li>
            <li>
              <a href="https://twitter.com/MatchdayN" target="_blank">
                <i class="fab fa-twitter" aria-hidden="true"></i>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="copyrights">
      <router-link :to="{ name: 'Terms' }">Terms</router-link>
      <router-link :to="{ name: 'Community_rules' }">Community rules</router-link>
      <p>
        &copy; 2020 {{ $store.state.projectName }}
      </p>
    </div>
    
  </div>

  <!-- ... end Right Sidebar -->
</template>

<script>
  import ls from "../../localstorage";
  // import { Timeline } from "vue-tweet-embed";
  import LiveMatchWidget from "./LiveMatchWidget";
  import UserSearch from "../UserSearch";

var pageData = {
  projectName: "",
  userData: {},
  id: null,
  avatar: "",
  signedIn: false,
  business: {},
  errorData: {},
  tweet: 0,
  spinnerData: {},
};
var loader;
export default {
  name: "RightSideBar",
  data() {
    return pageData;
  },
  components: {UserSearch, LiveMatchWidget},
  methods: {
    showLoading(options = {}) {
      let loader = this.spinnerData = {
        spinner: 'Jumper',
        isLoading: options.show !== undefined ? options.show : true ,
        color: '#00bfff',
        size: '60px',
        options: 'center relative',
      };
      return loader;
    },
    followBtn(index) {
      return 'followBtn_' + index;
    },
    getBusinesses() {
      this.showLoading();
      // this.checkFollowers();
      if (ls.checkStorage("fansData")) {
        let userData = ls.getStorage("fansData");
        this.axios
                .get(`businesses/recommended/${userData[0].id}`)
                .then(response => {
                  this.showLoading({show:false});
                  this.business = response.data.data.businesses.data;
                  // console.log('deals to follow',response.data.data.businesses.data);
                })
                .catch(error => {
                  this.showLoading({show:false});
                  // console.log(error.response);
                });
      }else {
        this.axios
                .get(`businesses/recommended/0`)
                .then(response => {
                  this.showLoading({show:false});
                  this.business = response.data.data.businesses.data;
                  // console.log('deals to follow',response.data.data.businesses.data);
                })
                .catch(error => {
                  this.showLoading({show:false});
                  // console.log(error.response);
                });
      }
    },
    followDeals(bid,index) {
      if(this.signedIn === true) {
        $("#followBtn_" + index).parent(".twitter-item").css('opacity','0.6');
        $("#followBtn_" + index).html('...');
        this.axios.post('user/follow/' + bid)
        .then(response => {
          // console.log(response);
          $("#followBtn_" + index).parent(".twitter-item").css('opacity','1');
          $("#followBtn_" + index).html('Unfollow');
          this.getBusinesses();
          this.errorData = {
            message: response.data.message,
            isError: "success"
          }
        })
        .catch(error => {
          // console.log(error.response);
          $("#followBtn_" + index).parent(".twitter-item").css('opacity','1');
          $("#followBtn_" + index).html('Follow');
          this.errorData = {
            message: error.response.data.message,
            isError: true
          }
        })
      }else {
        $('#loginBtn').click();
      }

    },
    unFollowDeals(bid,index) {
      if(this.signedIn === true) {
        $("#followBtn_" + index).parent(".twitter-item").css('opacity','0.6');
        $("#followBtn_" + index).html('...');
        this.axios.post('user/unfollow/' + bid)
        .then(response => {
          // console.log(response);
          $("#followBtn_" + index).parent(".twitter-item").css('opacity','1');
          $("#followBtn_" + index).html('Follow');
          this.getBusinesses();
          this.errorData = {
            message: response.data.message,
            isError: "success"
          }
        })
        .catch(error => {
          // console.log(error.response);
          $("#followBtn_" + index).parent(".twitter-item").css('opacity','1');
          $("#followBtn_" + index).html('Unfollow');
          this.errorData = {
            message: error.response.data.message,
            isError: true
          }
        })
      }else {
        $('#loginBtn').click();
      }

    },
    checkFollowers() {
      this.axios.get('/user/following')
      .then(response => {
        // console.log(response.data);

      })
      .catch(error => {
        // console.log(error.response);

      })
    },
    checkUserData(userData) {
      if (userData !== "" || userData !== "undefined") {
        this.userData = JSON.parse(userData);
        this.signedIn = true;
      }
    },
  },
  mounted() {
    this.projectName = this.$store.state.projectName;
    this.getBusinesses();
    var userData = localStorage.fansData;
    if (userData) {
      this.checkUserData(userData);
    }
  },
};
</script>

<style>
  .copyrights a {
    margin-right: 13px;
  }
  .copyrights p {
    width: 100%;
    font-size: 13px;
    padding-top: 3px;
  }
.copyrights {
  width: 100%;
  padding: 3px 10px;
}
</style>
