<template>
    <div class="user-search-box">
        <input class="users-search" type="search" id="userSearch" v-model="searchQuery" @keypress="searchUsers($event.target.value)" placeholder="Search for Users">
        <div class="users-search-dropdown" v-if="searchQuery !== '' ">
            <div v-if="noUser === false">
                <div v-for="(ssr,index) in searchedData" :key="index" @click="goToPage(ssr.username)" class="user-search-box-user">
                    <img :src=" ssr.avatar === null ? getAvatarLocal(ssr.username) : ssr.avatar">
                    <div class="user-search-box-details">
                        <div style="display: flex;width: 100%">
                            <h1>{{ ssr.firstname }} {{ ssr.lastname }}</h1>
                            <span v-if="ssr.team_details.length > 0">@{{ssr.username}}</span>
                        </div>
                        <p>
                            <span v-if="ssr.team_details.length <= 0">@{{ssr.username}}</span>
                            <span v-if="ssr.team_details.length > 0" style="display: flex;align-items: center">
                            <img :src="ssr.team_details[0].team_badge">
                            <span>{{ssr.team_details[0].team_name}}</span>
                        </span>
                        </p>
                    </div>
                </div>
            </div>
            <div v-else>
                <p style="padding: 10px;color: #fff;font-weight: bold;font-size: 20px">Opps! Seams you checked wrongly</p>
            </div>

        </div>
    </div>
</template>

<script>
    import avta from "@/functions/getAvatar";
    export default {
        name: "UserSearch",
        data() {
            return {
                userSearchDropdown: false,
                searchedData: [],
                noUser: false,
                searchQuery: '',
            }
        },
        methods: {
            getAvatarLocal(name) {
                // let userData = localStorage.fansData;
                return "https://api.adorable.io/avatars/285/" + name;
            },
            searchUsers(e) {
                const string = e
                if (string.length > 0) {
                    this.userSearchDropdown = true;
                    this.axios.get(`/users/search/${string}`)
                        .then((res) => {
                            const ssr = res.data.data

                            if (ssr.status === false) {
                                this.noUser = true
                                this.searchedData = []
                            }else {
                                this.noUser = false
                                this.searchedData = ssr.users
                            }

                            this.searchedData = ssr.users
                        })
                }else {
                    this.userSearchDropdown = false;
                }
            },
            goToPage(username) {
                this.$router.push('/profile/' + username);
                $('#userSearch').val('')
                this.searchUsers('')
            },
        },
    }
</script>

<style scoped>
    .user-search-box-user {
        width: 100%;
        float: left;
        display: flex;
        justify-content: space-between;
        padding: 10px;
        border-bottom: solid 1px #7c7b7b73;
        background-color: rgb(5, 56, 32);
        cursor:pointer;
    }
    .user-search-box-user:hover {
        background-color: rgb(16, 69, 44);
    }
    .user-search-box-user > img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
    .user-search-box-details {
        width: calc(100% - 45px);
        padding-left: 10px;
    }
    .user-search-box-details > span {
        font-weight: 100;
        position: relative;
        top: -2px;
        font-size: 11px;
        color: rgba(238, 238, 238, 0.75);
        max-width: calc(100% - 60px);
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .user-search-box-details h1 {
        font-size: 18px;
        font-weight: bold;
        color: #fff;
        margin: 0;
        padding-bottom: 4px;
        max-width: calc(100% - 40px);
        padding-right: 5px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .user-search-box-details p img {
        width: 17px;
        height: 17px;
        border-radius: 50%;
    }
    .user-search-box-details p span {
        padding:0 10px;
        width: 100%;
    }
    .user-search-box-details p {
        font-size: 12px;
        color: rgba(238, 238, 238, 0.75);
        margin: 0;
        display: flex;
        justify-content: space-between;
        padding-bottom: 4px;
        width: 100%;
    }
    .user-search-box-details span img {
        width: 25px;
        height: 25px;
        border-radius: 50%;
    }
    .user-search-box {
        position: relative;
        padding: 10px 0
    }
    .users-search-dropdown {
        width: 100%;
        height: 300px;
        max-height: 400px;
        overflow: auto;
        position: absolute;
        background-color: #1c4d36;
        box-shadow: 0 5px 10px rgba(0,0,0,.5);
        top: 31px;
        z-index: 2;
        border-radius: 30px;
        padding-top: 30px;
    }
    .users-search {
        position: relative;
        border-radius: 30px;
        height: 20px;
        border:none;
        outline: none;
        font-size: 13px;
        background-color: rgba(0, 0, 0, 0.42) !important;
        color: #eeeeee;
        z-index: 3;
    }
    .users-search::placeholder {
        font-size: 13px;
        color: #eeeeee;
    }
</style>